import React, { Component } from 'react'
import { Timeline, Preach, Year } from '../components/Timeline';

import events from '../data/events.json';

class Home extends Component {
  state = {renders: []}

  componentDidMount = () => {
    let result = []
    let currentYear = new Date().getFullYear()
    let tmpYear = currentYear
    console.log(currentYear)
    // events.sort((a, b) => Date.parse(a.date) < Date.parse(b.date))
    for (let element of events){
      let year = new Date(Date.parse(element.date)).getFullYear()
      if (year != tmpYear){
        result.push(<Year>{tmpYear}</Year>)
        tmpYear = year
      } 
      result.push(<Preach>{element}</Preach>)
    }
    result.push(<Year>{tmpYear}</Year>)

    this.setState({renders: result})
  }

  render() {
    return (
      <Timeline>
        {this.state.renders}
      </Timeline>
    )
  }
}

export default Home;
