import React, { Component, Fragment } from 'react'
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';

import { BrowserView, MobileView } from 'react-device-detect'

import 'react-vertical-timeline-component/style.min.css';
import { Typography } from '@material-ui/core';
import ministeries from '../data/ministeries';
import Moment from 'react-moment';

import 'moment/locale/es';

function Year(props){
  const date = props.children
  return (
    <VerticalTimelineElement
      iconStyle={{ background: '#14aa9c', color: '#fff' }}
      icon={<div className="year-container">
        <BrowserView>
          <Typography variant="h6">{date}</Typography>
        </BrowserView>
        <MobileView>
          <Typography variant="h6">{date%100}</Typography>
        </MobileView>
      </div>}
    />
  )
}

// https://www.youtube.com/embed/IBi3d7aFL54
function Youtube(props) {
  return <iframe width="100%" src={props.link} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
}

class Preach extends Component {
  componentDidMount() {
    const {id, type} = this.props.children
    const { color } = ministeries[type]
    document.getElementById("timeline-" + id).childNodes[0].childNodes[1].style.borderTop = "3px solid "+ color;
  }

  render(){
    const { id, name, preacher, link, caption, type, date } = this.props.children
    const { color, defaultPreacher, icon } = ministeries[type]
    return (
      <VerticalTimelineElement
        id={"timeline-" + id}
        date={<Moment format="MMMM DD, YYYY">{date}</Moment>}
        iconStyle={{ background: color, color: '#fff' }}
        icon={icon}
      >
        <div className="timeline-date">
          <Moment fromNow>{date}</Moment>
        </div>
        <Typography className="vertical-timeline-element-title" variant="h5">{name}</Typography>
        <Typography className="vertical-timeline-element-subtitle" variant="subtitle1">{preacher? preacher: defaultPreacher}</Typography>
        <br/>
        <Youtube link={link}/>
        <Typography variant="caption">{caption}</Typography>
        <br/>
      </VerticalTimelineElement>)
  }
}

function Article(props) {
  return <></>
}

class Timeline extends Component {
  render() {
    return (
        <VerticalTimeline>
          {this.props.children}
        </VerticalTimeline>
    )
  }
}

export {Timeline, Preach, Article, Year}